@font-face {
  font-family: 'Brown2018-Regular';
  font-style: normal;
  font-weight: 400;
  src: local("Brown2018-Regular"), url(/static/fonts/Brown2018-Regular.ttf) format("truetype"); }

.home_container {
  color: #212830;
  background-color: white;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  font-family: "Brown2018-Regular";
  line-height: 1.55; }
  .home_container--first {
    width: 38%;
    padding-left: 12%; }
  .home_container--second {
    width: 60%; }
  .home_container_text {
    width: 70%;
    margin: 130px auto; }
    .home_container_text-title {
      font-size: 32px;
      line-height: 1.25; }
      .home_container_text-title span {
        color: #0281e6; }
    .home_container_text-subtitle {
      color: #b2b2b2;
      font-size: 18px;
      padding-top: 4%; }
  .home_container_image {
    display: flex;
    background: url("/static/Oval.png") right center no-repeat;
    height: 100%; }
    .home_container_image img {
      width: 80%;
      margin: auto; }
