@font-face {
  font-family: 'Brown2018-Regular';
  font-style: normal;
  font-weight: 400;
  src: local("Brown2018-Regular"), url(/static/fonts/Brown2018-Regular.ttf) format("truetype"); }

.service_container {
  width: 80%;
  margin: 4% auto;
  padding-bottom: 30px;
  font-family: "Brown2018-Regular";
  line-height: 1.55; }
  .service_container--first, .service_container--second, .service_container--third {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 100px 60px; }
  .service_container--first .service_container_text {
    padding-left: 100px;
    font-family: "Brown2018-Regular"; }
  .service_container--second .service_container_text,
  .service_container--third .service_container_text {
    padding-right: 100px; }
  .service_container_image, .service_container_text {
    width: 48%; }
  .service_container_text {
    margin: auto; }
    .service_container_text-title {
      font-size: 26px;
      line-height: 1.25; }
    .service_container_text-subtitle {
      font-size: 18px;
      color: #b2b2b2;
      padding: 30px 0; }
    .service_container_text-cta {
      cursor: pointer;
      color: #eb0032; }
      .service_container_text-cta:after {
        content: "";
        background: url("/static/right-arrow.svg") bottom center no-repeat;
        display: inline-block;
        width: 20px;
        height: 15px;
        margin-left: 10px; }
  .service_container_image img {
    width: 100%; }
