@font-face {
  font-family: 'Brown2018-Regular';
  font-style: normal;
  font-weight: 400;
  src: local("Brown2018-Regular"), url(/static/fonts/Brown2018-Regular.ttf) format("truetype"); }

.container {
  font-family: "Brown2018-Regular";
  font-size: 16px;
  color: white;
  font-family: "Brown2018-Regular";
  line-height: 1.55; }
  .container--first {
    background-color: #000;
    width: 100%;
    height: 30px;
    display: flex;
    justify-content: space-between;
    position: fixed;
    top: 0; }
    .container--first--hidden {
      top: -50px; }
  .container--second {
    background-color: #191e25;
    width: 100%;
    height: 80px;
    position: fixed;
    top: 30px; }
    .container--second--scrolled {
      top: 0; }

.nav--left {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  width: 28%;
  cursor: pointer; }

.nav_img {
  height: 15px;
  margin: auto; }

.nav_items {
  margin: auto;
  opacity: .8;
  transition: opacity .1s ease-out;
  cursor: pointer; }
  .nav_items:hover {
    opacity: 1; }
  .nav_items-button {
    background-color: transparent;
    color: white;
    border-radius: 15px;
    cursor: pointer; }

.nav--right {
  display: flex;
  justify-content: space-around;
  width: 17%;
  margin-right: 8%;
  cursor: pointer; }

.menu {
  list-style: none;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: auto;
  width: 60%;
  height: 100%; }
  .menu_items {
    font-family: "Brown2018-Regular";
    margin: auto;
    opacity: .8;
    transition: opacity .1s ease-out;
    cursor: pointer; }
    .menu_items:hover {
      opacity: 1; }
  .menu-image {
    margin: auto; }
  .menu-search:after {
    content: "";
    background: url("/static/search.png") top center no-repeat;
    display: inline-block;
    width: 20px;
    height: 20px;
    margin-left: 10px; }
